inline-block;\n  text-decoration: none;\n  color: #fff;\n  min-height: 44px;\n  min-width: 44px;\n}\n\n.nav_menu > ul > li > a:hover {\n  color: #ebb22f;\n}\n\n.nav_menu > ul > li > a::after {\n  content: \"\";\n  display: block;\n  padding-bottom: 0.5rem;\n  border-bottom: 0.1rem solid #ebb22f;\n  transform: scale(0);\n  transition: 0.2s linear;\n}\n\n.nav_menu > ul > li > a:hover::after {\n  transform: scaleX(0.5);\n}\n\n/* Humberg Button */\nbutton.hamburger-menu {\n  min-height: 44px;\n  min-width: 44px;\n  padding: 10px;\n  padding-left: 90px;\n  background: none;\n  border: none;\n  cursor: pointer;\n  display: none;\n}\n\nbutton.hamburger-menu > i {\n  color: #fff;\n  font-size: 1.5rem;\n  min-width: 44px;\n  min-height: 44px;\n}\n\nbutton.hamburger-menu > i.icon-close {\n  display: none;\n}\n\n/* Hero Section */\nheader {\n  background-position: center center;\n  background-size: cover;\n  background-repeat: no-repeat;\n  font-family: \"Kanit\", sans-serif;\n  letter-spacing: 5px;\n  text-shadow: 0 0 4px #000;\n}\n\n.hero {\n  background: url(\"../public/images/heros/hero-image.jpg\");\n  height: 85vh;\n  width: 100%;\n  background-position: center center;\n  background-size: cover;\n  background-repeat: no-repeat;\n  position: relative;\n  font-family: \"Kanit\", sans-serif;\n  letter-spacing: 5px;\n  text-shadow: 0 0 4px #000;\n}\n\n.opacity-header {\n  background-color: #222831;\n  width: 100%;\n  height: 100%;\n  opacity: 0.7;\n  position: absolute;\n  top: 0;\n  left: 0;\n}\n\n.hero-header {\n  position: absolute;\n  top: 0;\n  left: 0;\n  display: flex;\n  justify-content: center;\n  width: 100%;\n  height: 100%;\n  align-items: center;\n  flex-direction: column;\n  text-align: center;\n}\n\n.hero-header > h1 {\n  color: #fff;\n  font-size: 3.5rem;\n  margin: 0px;\n  padding: 0px;\n}\n\n.hero-header > h1 > span {\n  color: #ffce10;\n}\n\n.hero-header > h2 {\n  color: #fff;\n  margin: 0px;\n  padding: 10px 50px;\n  font-size: 1.1rem;\n  line-height: 2rem;\n  text-transform: uppercase;\n}\n\n.hero-header > a {\n  min-height: 44px;\n  min-width: 44px;\n  text-decoration: none;\n  color: #333;\n  text-shadow: none;\n}\n\n.button {\n  background-color: #ffce10;\n  padding: 4px 5px 1px 8px;\n  border-radius: 10px;\n  transition: 0.3;\n  text-align: center;\n  font-family: \"Kanit\", sans-serif;\n  display: inline-block;\n  min-width: 44px;\n  min-height: 44px;\n}\n\n.button:hover {\n  font-size: 1.2rem;\n  font-style: bold;\n  padding: 15px;\n  background-color: #eeeeee;\n  color: black;\n}\n\n/* Main content */\n#main-content {\n  background-color: #eeeeee;\n  color: #000;\n  font-family: \"Kanit\", sans-serif;\n  width: 100%;\n}\n\n#main-content > .wrapper {\n  padding: 50px 0px;\n}\n\n#main-content > h1 {\n  text-align: center;\n  font-size: 2.3rem;\n  margin: 0px;\n  padding: 0px;\n  font-family: \"Kanit\", sans-serif;\n}\n\n#main-content > p {\n  text-align: center;\n  color: #000;\n  margin: 0px;\n  padding: 0px;\n  margin-bottom: 50px;\n}\n\n#wrapper-restaurant {\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  gap: 20px;\n}\n\n#wrapper-restaurant-favorite {\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  gap: 20px;\n  min-height: 60vh;\n}\n\n.restaurant-item_not_found > h1 {\n  display: flex;\n  align-items: center;\n  text-align: center;\n  font-size: 1rem;\n}\n\n.restoran_list {\n  border: 1px solid #ddd; \n  vertical-align: top; \n  box-sizing: border-box;\n  transition: transform 0.3s; \n  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);\n  font-family: \"Kanit\", sans-serif;\n  width: 100%;\n}\n\n.restoran_list_img {\n  border-top-left-radius: 10px;\n  border-top-right-radius: 10px;\n}\n\n.restoran_list {\n  border-radius: 10px;\n}\n\n.restoran_list > a {\n  min-width: 44px;\n  min-height: 44px;\n}\n\n.restoran_list:hover {\n  transform: translateY(-6px);\n}\n\n.restoran_list_img {\n  width: 100%; \n  height: auto;\n}\n\n.restoran_list_content {\n  padding: 10px;\n  background-color: #f9f9f9;\n  position: relative;\n  border-radius: 10px;\n}\n\n.restoran_list_title {\n  min-width: 44px;\n  min-height: 44px;\n  transition: font-size 0.3s;\n  font-size: 24px;\n  color: #666; \n  text-decoration: none; \n}\n\n.restoran_list_title:hover,\n.restoran_list_title:focus {\n  font-size: 30px;\n  color: #000;\n}   \n\n.restoran_list_rating {\n  color: #333;\n  display: inline-block;\n  min-width: 44px;\n  min-height: 44px;\n  padding: 5px;\n  font-size: 20px;\n}\n\n.restoran_list_rating_value{\n  display: inline-block;\n  font-size: 1.3rem;\n  min-width: 44px;\n  min-height: 44px;\n  font-weight: bold;\n  color: #ffce10;\n  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);\n}\n\n.restoran_city {\n  position: absolute;\n  margin: 10px;\n  top: 10px; \n  right: 0;\n  left: auto; \n  color: #fff;\n  background-color: #393E46;\n  border-radius:10px;\n  display: inline-block; \n  padding: 5px 20px; \n}\n\n.restoran_list_desc {\n  font-size: 16px; \n  color: #555; \n}\n\n/* Detail Restoran */\n.restoran {\n  margin: 0 auto;\n  width: 100%;\n  max-width: 800px;\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 16px 16px;\n}\n\n.restoran_detail_info {\n  width: 100%;\n}\n\n.restoran_description > p {\n  text-align: justify;\n}\n\n.wrapper > .restoran_detail_title > h2 {\n  display: flex;\n  justify-content: center;\n  font-size: 2rem;\n}\n\n.wrapper > .restoran_detail_poster {\n  justify-items: center;\n  max-width: 800px;\n}\n\n.restoran_detail_menu {\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  gap: 20px;\n}\n\n.restoran_detail_menu_makan {\n  border: 1px;\n  border-radius: 5px;\n  background-color: #00ADB5;\n}\n\n.restoran_detail_menu_makan > h4 {\n  text-align: center;\n  color: #ffffff;\n  font-size: 1.5rem;\n}\n\n.restoran_detail_menu_makan > ul {\n  color: #222831;\n  font-weight: bold;\n  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);\n}\n\n/* Review */\n#add-review-form {\n  max-width: 400px;\n  margin: 0 auto;\n}\n\n#review-list {\n  margin-bottom: 20px;\n}\n\n#review-list label {\n  display: block;\n  margin-bottom: 5px;\n  font-weight: bold;\n}\n\n#review-list input[type=\"text\"],\n#review-list textarea {\n  min-width: 44px;\n  min-height: 44px;\n  width: 100%;\n  padding: 10px;\n  border: 1px solid #ccc;\n  border-radius: 5px;\n  box-sizing: border-box;\n  margin-bottom: 10px;\n  font-weight: bold;\n  font-family: \"Nunito Sans\", sans-serif;\n}\n\n#review-list textarea {\n  height: 150px;\n}\n\nbutton[type=\"submit\"] {\n  background-color: #007bff;\n  color: #fff;\n  border: none;\n  padding: 10px 20px;\n  border-radius: 5px;\n  cursor: pointer;\n  transition: background-color 0.3s;\n  min-width: 44px;\n  min-height: 44px;\n}\n\nbutton[type=\"submit\"]:hover {\n  background-color: #0056b3;\n}\n\n.restoran_detail_review > ul {\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  gap: 10px;\n  padding: 0;\n  list-style-type: none;\n}\n\n.restoran_detail_review > ul > li {\n  border: 1px;\n  border-radius: 5px;\n  background-color: #ebb22f;\n  padding: 0px 5px 0px 10px;\n  color: #000000;\n  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.1);\n}\n\n/* Like Button */\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #393E46;\n\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n\n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n\n/* Footer Section */\nfooter {\n  bottom: 0;\n  width: 100%;\n  background-color: #222831;\n  color: #fff;\n  text-align: center;\n}\n\n/* Skip to Content */\n.skip-to-content {\n  font-family: \"Nunito Sans\", sans-serif;\n  position: absolute;\n  top: -60px;\n  left: 0;\n  background-color: #ebb22f;\n  color: white;\n  padding: 8px;\n  min-width: 44px;\n  min-height: 44px;\n  z-index: 9999;\n}\n\n.skip-to-content:focus {\n  top: 0;\n}\n\n/* loading */\n@keyframes putar {\n  from {\n    transform: rotate(0deg);\n  }\n  to {\n    transform: rotate(360deg);\n  }\n}\n\n#loading {\n  display: none;\n  width: 50px;\n  height: 50px;\n  border: solid 5px #ccc;\n  border-top-color: #ff6a00;\n  border-radius: 100%;\n  position: fixed;\n  left: 0;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  margin: auto;\n  animation: putar 2s linear infinite;\n}\n"],"sourceRoot":""}]);
